'use strict';

/**
 * @fileoverview TensorData class to store tensors like NMR data and such.
 * @module
 */

import _ from 'lodash';
import * as mjs from 'mathjs';
import * as THREE from 'three';
import { floatEqual } from './utils.js';

const efg2hz = 234964.77815245767;
const isc2hz = 1.6784031762379067e-16;    // hbar/(2*pi)*1e19
const PI = mjs.pi



/**
 * 
 * @param {*} alpha in radians 
 * @param {*} beta in radians
 * @param {*} gamma in radians
 * @param {*} PAS (principal axis system)
 * @param {*} mode (e.g. 'zyz')
 * @param {*} active (true: active rotation, false: passive rotation)
 * 
 * @returns {mathjs.Matrix} rotation matrix
 * 
 * TODO: implement other modes
 */

function rotateTensor(alpha, beta, gamma, PAS, mode, active) {
    if (mode != 'zyz')
        throw new Error('Only zyz mode is implemented so far');

    
    if (mode === 'zyz') {

        let rz1 = mjs.matrix([
            [mjs.cos(alpha), -mjs.sin(alpha), 0],
            [mjs.sin(alpha), mjs.cos(alpha), 0],
            [0, 0, 1]
        ]);
        let ry2 = mjs.matrix([
            [mjs.cos(beta), 0, mjs.sin(beta)],
            [0, 1, 0],
            [-mjs.sin(beta), 0, mjs.cos(beta)]
        ]);
        let rz3 = mjs.matrix([
            [mjs.cos(gamma), -mjs.sin(gamma), 0],
            [mjs.sin(gamma), mjs.cos(gamma), 0],
            [0, 0, 1]
        ]);

        if (active)
            return mjs.multiply(mjs.multiply(rz1, ry2), rz3);
        else {
            rz1 = mjs.inv(rz1);
            ry2 = mjs.inv(ry2);
            rz3 = mjs.inv(rz3);
            return mjs.multiply(mjs.multiply(rz3, ry2), rz1);
        }
    }
    

}


class TensorData {

    /**
     * Create a TensorData object, to store whole tensors, 
     * diagonalise their symmetric part and return derived quantities
     * 
     * @param {Array | mathjs.Matrix | TensorData} M     Tensor in 3x3 matrix form
     * 
     */
    constructor(M) {

        if (M instanceof TensorData)
            M = M._M;
        else if (M instanceof Array)
            M = mjs.matrix(M);

        this._M = M;

        var MT = mjs.transpose(M);
        this._Msymm = mjs.divide(mjs.add(M, MT), 2.0);
        this._Masymm = mjs.subtract(M, this._Msymm);

        // Diagonalize
        var eigs = mjs.eigs(this._Msymm);
        // Sort by eigenvalue magnitude
        var evecs = mjs.transpose(eigs.vectors._data);
        eigs = _.zip(eigs.values._data, evecs);
        eigs = _.sortBy(eigs, function(x) {
            return x[0];
        });
        eigs = _.unzip(eigs);

        this._evals = eigs[0];

        // Make it right-handed
        evecs = eigs[1];
        evecs[2] = mjs.cross(evecs[0], evecs[1]);
        this._evecs = mjs.transpose(evecs);

        // Isotropy
        this._iso = mjs.mean(this._evals);

        // Haeberlen order
        var iso = this._iso;
        var haeb = _.zip(_.range(3), this._evals);
        haeb = _.sortBy(haeb, function(x) {
            return Math.abs(x[1] - iso);
        });

        this._haeb_evals = [
            this._evals[haeb[1][0]],
            this._evals[haeb[0][0]],
            this._evals[haeb[2][0]]
        ];

        this._haeb_evecs = mjs.transpose([
            evecs[haeb[1][0]],
            evecs[haeb[0][0]],
            mjs.cross(evecs[haeb[1][0]], evecs[haeb[0][0]])
        ]);

        // NQR order
        // TODO this needs more testing!
        var iso = this._iso;
        var nqr = _.zip(_.range(3), this._evals);
        nqr = _.sortBy(nqr, function(x) {
            return Math.abs(x[1] - iso);
        });

        this._nqr_evals = [
            this._evals[nqr[0][0]],
            this._evals[nqr[1][0]],
            this._evals[nqr[2][0]]
        ];

        this._nqr_evecs = mjs.transpose([
            evecs[nqr[1][0]],
            evecs[nqr[0][0]],
            mjs.cross(evecs[nqr[1][0]], evecs[nqr[0][0]])
        ]);



    }

    get data() {
        return JSON.parse(JSON.stringify(this._M._data));
    }

    get symmetric() {
        return JSON.parse(JSON.stringify(this._Msymm._data));
    }

    get asymmetric() {
        return JSON.parse(JSON.stringify(this._Masymm._data));
    }

    get eigenvalues() {
        return Array.from(this._evals);
    }

    get eigenvectors() {
        return JSON.parse(JSON.stringify(this._evecs));
    }

    get haeberlen_eigenvalues() {
        return Array.from(this._haeb_evals);
    }

    get haeberlen_eigenvectors() {
        return JSON.parse(JSON.stringify(this._haeb_evecs));
    }

    get nqr_eigenvalues() {
        return Array.from(this._nqr_evals);
    }

    get nqr_eigenvectors() {
        return JSON.parse(JSON.stringify(this._nqr_evecs));
    }

    get isotropy() {
        return this._iso;
    }

    get anisotropy() {
        return this._haeb_evals[2] - (this._haeb_evals[0] + this._haeb_evals[1]) / 2.0;
    }

    get reduced_anisotropy() {
        return this._haeb_evals[2] - this._iso;
    }

    get asymmetry() {
        var ra = this.reduced_anisotropy;
        return (this._haeb_evals[1] - this._haeb_evals[0]) / ra;
    }

    get span() {
        return this._evals[2] - this._evals[0];
    }

    get skew() {
        var s = this.span;
        return 3 * (this._evals[1] - this._iso) / s;
    }

    /**
     * Rotate the TensorData by a given basis, either as passive or active
     * transformation. Returns the rotated TensorData (does not modify this in
     * place). Default is passive. The convention is such that for a symmetric
     * tensor,
     *
     * T.rotate(T.eigenvectors)
     *
     * returns the diagonalised tensor.
     * 
     * @param  {Array | mathjs.Matrix | TensorData}  basis  Basis to rotate into
     * @param  {Boolean}                             active If true, make it an active transformation (default is false)
     * 
     * @return {TensorData}                                 Rotated tensor
     */
    rotate(basis, active = false) {
        // Rotate the tensor by the given basis of vectors
        if (basis instanceof mjs.Matrix)
            basis = basis._data;
        if (basis instanceof TensorData)
            basis = basis._M._data;

        var bR = basis;
        var bL = mjs.transpose(basis);

        if (active) {
            bR = bL;
            bL = basis;
        }
        var rdata = mjs.multiply(bL, this._M._data, bR);

        return new TensorData(rdata);
    }


    /**
     * Calculate the Euler angles for the TensorData. Returns an array of
     * [alpha, beta, gamma] in degrees.
     * 
     * This function is based on the code from TensorView for MATLAB by
     * Leo Svenningsson and Leonard J. Mueller
     * https://doi.org/10.1016/j.ssnmr.2022.101849
     * 
     * @param {String} convention  Convention to use (default is 'haeberlen')
     * @param {Boolean} active     If true, make it an active transformation (default is false)
     * @param {String} mode       Mode to use: either 'zyz' or 'zxz' (default is 'zyz')
     * 
     * @return {Array}             Array of [alpha, beta, gamma] in degrees
     * 
     * TODO: check the conventions!
     * TODO: check active vs passive
     * TODO: implement other modes (zxz)
     */
    euler(convention = 'haeberlen', active = false, mode = 'zyz') {
        // Eigenvalues and eigenvectors of the symmetric part of the tensor
        const evals = this._evals;
        const evecs = this._evecs;
        let alpha = 0
        let beta = 0
        let gamma = 0
        
        // number of unique eigenvalues (within tolerance)
        const tol = 1e-4;
        const degeneracies = _.countBy(evals, function(x) {
            return mjs.round(x/tol)*tol;
        });
        const n_unique = _.keys(degeneracies).length;
        const symm = evals.length - n_unique;
        

        // TODO check these conventions! 
        // Sorting the eigenvalues and eigenvectors
        // sort mode: 'haeberlen' = 'ascending', 'nqr' = 'absascending', 'descending'
        // switch

        // Sort the eigenvalues
        const sorted_evals = _.sortBy(evals, function(x) {
            if (convention === 'ascending' || convention === 'haeberlen')
                return x;
            else if (convention === 'absascending' || convention === 'nqr')
                return mjs.abs(x);
            else if (convention === 'descending')
                return -x;
        });

        // Sort the eigenvectors accordingly
        let U = [];
        for (let i = 0; i < 3; i++) {
            let idx = evals.indexOf(sorted_evals[i]);
            U.push(evecs[idx]);
        }

        // PAS = principal axis system
        // it has the eigenvalues on the diagonals, zeros elsewhere
        const PAS = new mjs.diag(sorted_evals);
        // PASv is just the eigenvalues in a vector
        const PASv = sorted_evals;

        if (mode === 'zyz') {

            console.log('zyz')
            // ZYZ Euler angles
            // initialising the angles
            let alpha1 = 0
            let beta1 = 0
            let gamma1 = 0

            beta1 = mjs.acos(U[2][2]);

            if (U[2][2] == 1) {
                // beta1 = 0
                alpha1 = mjs.acos(U[0][0]);
                gamma1 = 0;
            } else {

                alpha1 = mjs.atan2(U[2][1] / mjs.sin(beta1), U[2][0] / mjs.sin(beta1));
                gamma1 = mjs.atan2(U[1][2] / mjs.sin(beta1), -U[0][2] / mjs.sin(beta1));
            }

            let alpha = alpha1;
            let beta = beta1;
            let gamma = gamma1;

            let M = rotateTensor(alpha, beta, gamma, PAS, mode, active);
            // U PAS U^-1
            let CF = mjs.multiply(mjs.multiply(U, PAS), mjs.inv(U));

            // now let's check to make sure M and Cf are nearly the same
            // this is because the eigenvalue solver is not always perfect
            // and we want to make sure that the angles are correct
            if (mjs.norm(mjs.subtract(M, CF)) > 1e-4) {
                // if they are not the same, we can simply flip all the signs of U
                // and try again
                U = mjs.multiply(-1, U);
                beta1 = mjs.acos(U[2][2]);
                
                if (U[2][2] == 1) {
                    // beta1 = 0
                    alpha1 = mjs.acos(U[0][0]);
                    gamma1 = 0;
                }
                else {
                    alpha1 = mjs.atan2(U[2][1] / mjs.sin(beta1), U[2][0] / mjs.sin(beta1));
                    gamma1 = mjs.atan2(U[1][2] / mjs.sin(beta1), -U[0][2] / mjs.sin(beta1));
                }

                alpha = alpha1;
                beta = beta1;
                gamma = gamma1;
            }

            console.log('evals', evals)
            console.log('symmetry: ', symm)

            // -- handle degenerate cases --
            // If there are three degenerate eigenvalues, then we have spherical symmetry
            // and set all Euler angles to zero
            if (symm === 2)
                return [0, 0, 0];
                // If there are two degenerate eigenvalues, then we have axial symmetry
                // and things become a bit tricky
            const tol = 1e-4;
            if (symm === 1) {
                // if the second and third eigenvalues are not equal
                // then we can just set gamma = 0
                if (!floatEqual(evals[2], evals[1], tol)) {
                    gamma = 0;
                } else {
                    // then the second and third eigenvalues are equal

                    // let's round the symmetric part of the tensor
                    // to make sure that we don't get any numerical errors
                    let symT = mjs.round(this._Msymm, 14);
                    // similar for the D tensor
                    let symD = mjs.round(D, 14);

                    // if elements [2,1] and [1,0] are both zero
                    if (floatEqual(symT[2][1], 0, tol) && floatEqual(symT[1][0], 0, tol)) {
                        if (abs(mjs.asin(sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])))) === PI/2) {
                            gamma = mjs.asin(sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                            alpha = 0;
                            beta  = 0;

                            M = rotateTensor(alpha, beta, gamma, PAS, mode, active);

                            // check if the rotation worked
                            if (mjs.norm(mjs.subtract(M, symT)) > tol) {
                                // if not, let's try the other solution
                                gamma = mjs.asin(-sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                                alpha = 0;
                                beta  = 0;
                            }
                        } 
                        else {
                            gamma = mjs.asin(sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                            beta  = mjs.asin(sqrt((symT[2][2]-symD[2])/(symD[0]-symD[2] + (symD[1]-symD[0])*mjs.pow(mjs.sin(gamma), 2))));
                            alpha = 0;
                            M = rotateTensor(alpha, beta, gamma, PAS, mode, active);
                            // check if the rotation worked
                            if (mjs.norm(mjs.subtract(M, symT)) > tol) {
                                // if not, let's try the other solution for gamma (beta depends on gamma)
                                gamma = mjs.asin(-sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                                beta  = mjs.asin( sqrt((symT[2][2]-symD[2])/(symD[0]-symD[2] + (symD[1]-symD[0])*mjs.pow(mjs.sin(gamma), 2))));
                            }
                            M = rotateTensor(alpha, beta, gamma, PAS, mode, active);
                            // check if the rotation worked
                            if (mjs.norm(mjs.subtract(M, symT)) > tol) {
                                // if not, let's try the other solution for beta (alpha and gamma are the same)
                                gamma = mjs.asin( sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                                beta  = mjs.asin(-sqrt((symT[2][2]-symD[2])/(symD[0]-symD[2] + (symD[1]-symD[0])*mjs.pow(mjs.sin(gamma), 2))));
                            }
                            M = rotateTensor(alpha, beta, gamma, PAS, mode, active);
                            // check if the rotation worked
                            if (mjs.norm(mjs.subtract(M, symT)) > tol) {
                                // if not, let's try the other solution for gamma and beta (alpha is the same)
                                gamma = mjs.asin(-sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                                beta  = mjs.asin(-sqrt((symT[2][2]-symD[2])/(symD[0]-symD[2] + (symD[1]-symD[0])*mjs.pow(mjs.sin(gamma), 2))));
                            }
                        }
                    } // symT elements [2,1] and [1,0] are both zero
                    else {
                        gamma = mjs.asin(sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                        beta = mjs.atan2(-symT[2][1]/(mjs.sin(gamma)*mjs.cos(gamma)*(symD[0]-symD[1])), symT[1][0]/(mjs.sin(gamma)*mjs.cos(gamma)*(symD[0]-symD[1])));
                        alpha = 0;
                        
                        M = rotateTensor(alpha, beta, gamma, PAS, mode, active);
                        // check if the rotation worked
                        if (mjs.norm(mjs.subtract(M, symT)) > tol) {
                            // if not, let's try the other solution for gamma (beta depends on gamma)
                            gamma = mjs.asin(-sqrt((symT[1][1]-symD[1])/(symD[0]-symD[1])));
                            beta = mjs.atan2(-symT[2][1]/(mjs.sin(gamma)*mjs.cos(gamma)*(symD[0]-symD[1])), symT[1][0]/(mjs.sin(gamma)*mjs.cos(gamma)*(symD[0]-symD[1])));
                        }
                    }
                }  //end of the second and third eigenvalues are equal 
            } // end of doubly degenerate case


            // now we check if the angles are in the right range
            
            alpha = mjs.mod(alpha, 2*PI);
            beta  = mjs.mod(beta,  2*PI);
            gamma = mjs.mod(gamma, 2*PI);

            if (beta > PI) {
                alpha = alpha - PI;
                alpha = mjs.mod(alpha, 2*PI);
                beta = 2*PI - beta;
            }

            if (beta >= (PI / 2)) {
                alpha = alpha + PI;
                alpha = mjs.mod(alpha, 2*PI);
                beta = PI - beta;
                beta = mjs.mod(beta, 2*PI);
                gamma = PI - gamma;
                gamma = mjs.mod(gamma, 2*PI);
            }

            if (gamma >= PI) {
                gamma = gamma - PI;
            }
            console.log('alpha = ' + mjs.round(alpha * 180 / PI, 1));
            console.log('beta  = ' + mjs.round(beta * 180 / PI, 1));
            console.log('gamma = ' + mjs.round(gamma * 180 / PI, 1));
            
        } // end of 'zyz' mode

        // TODO: why is alpha zero here? when it's not zero in the previous console.log?
        // console.log('alpha = ' + alpha);
        // console.log('beta = ' + beta);
        // console.log('gamma = ' + gamma);
        // convert to degrees
        alpha = alpha * 180 / PI;
        beta = beta * 180 / PI;
        gamma = gamma * 180 / PI;


        return [alpha, beta, gamma];
    } 

        


    /**
     * Convert this TensorData to return a clone that has been converted from
     * atomic units to Hertz, assuming it's an Electric Field Gradient tensor.
     * 
     * @param  {Number} Q       Quadrupolar moment of the given nucleus (barn)
     * 
     * @return {TensorData}     Converted tensor
     */
    efgAtomicToHz(Q) {

        // Clone self, then multiply all the necessary quantities
        var clone = _.clone(this);

        var k = efg2hz*Q;

        clone._M = mjs.multiply(this._M, k);
        clone._Msymm = mjs.multiply(this._Msymm, k);
        clone._Masymm = mjs.multiply(this._Masymm, k);

        clone._iso = this._iso*k;

        clone._evals = mjs.multiply(this._evals, k);
        clone._haeb_evals = mjs.multiply(this._haeb_evals, k);

        return clone;
    }

    /**
     * Convert this TensorData to return a clone that has been converted from
     * atomic units to Hertz, assuming it's an Indirect Spin-spin Coupling 
     * tensor.
     * 
     * @param  {Number} g1  Gyromagnetic ratio of the first atom
     * @param  {Number} g2  Gyromagnetic ratio of the second atom
     * 
     * @return {TensorData}     Converted tensor
     */
    iscAtomicToHz(g1, g2) {

        // Clone self, then multiply all the necessary quantities
        var clone = _.clone(this);

        var k = isc2hz*g1*g2;

        clone._M = mjs.multiply(this._M, k);
        clone._Msymm = mjs.multiply(this._Msymm, k);
        clone._Masymm = mjs.multiply(this._Masymm, k);

        clone._iso = this._iso*k;

        clone._evals = mjs.multiply(this._evals, k);
        clone._haeb_evals = mjs.multiply(this._haeb_evals, k);

        return clone;        
    }

}

